import { useEffect } from 'react';
import { gsap } from 'gsap';

import PoolImage from './PoolImage';

import './second-pool.scss';

const SecondPool = () => {
  useEffect(() => {
    gsap
      .timeline({
        scrollTrigger: {
          trigger: '.second-pool-header',
          start: 'top top',
          end: 'bottom',
          pin: true,
          scrub: true,
        },
        opacity: 1,
      })
      .from('.second-pool-header', {
        y: 100,
        opacity: 0,
      })
      .to('.second-pool-header', {
        opacity: 1,
        y: 0,
      })
      .to('.second-pool-header', {
        opacity: 0,
        y: -100,
      });

    gsap
      .timeline({
        scrollTrigger: {
          trigger: '.second-pool-text',
          start: 'top top',
          end: '1480',
          pin: true,
          scrub: true,
        },
        opacity: 1,
      })
      .from('.second-pool-text', {
        y: 100,
        opacity: 0,
        duration: 500,
      })
      .to('.second-pool-text', {
        opacity: 1,
        y: 0,
        duration: 800,
      })
      .to('.second-pool-text', {
        opacity: 0,
        y: -200,
        duration: 500,
      });

    gsap
      .timeline({
        scrollTrigger: {
          trigger: '.pool-img-dots-wrapper',
          start: '200',
          end: '2600',
          scrub: true,
        },
        opacity: 1,
      })
      .from('.pool-img-dots-wrapper', {
        opacity: 0,
      })
      .to('.pool-img-dots-wrapper', {
        opacity: 1,
      })
      .to('.pool-img-dots-wrapper', {
        opacity: 0,
      });
  }, []);

  return (
    <>
      <div className="second-pool-header flex-column">
        <h5></h5>
        <h2> .</h2>
      </div>
      <div className="second-pool-text flex-center">
        <p>
          <strong>
                 Compass
            Pools
          </strong>
          <br />
           INPOOL CONCEPT
        </p>
      </div>
      <div className="second-pool second-pool-wrapper">
        <div className="second-pool-img-wrapper flex-center">
          <div className="pool-img-dots-wrapper">
            <div data-text="" className="img-dot img-dot-1"></div>
            <div data-text="MaxiRIB" className="img-dot img-dot-2"></div>
            <div data-text="" className="img-dot img-dot-3"></div>
            <div data-text="INPOOL" className="img-dot img-dot-4"></div>
            <div data-text=" INPOOLASMCONTROL" className="img-dot img-dot-5"></div>
            <div data-text=" Besgo" className="img-dot img-dot-6"></div>
            <div data-text=". INPOOLDispenseControl" className="img-dot img-dot-7"></div>
            <div data-text="" className="img-dot img-dot-8"></div>
            <div data-text="" className="img-dot img-dot-9"></div>
            <div data-text="  Vantage" className="img-dot img-dot-10"></div>
            <div data-text=" Vantage" className="img-dot img-dot-11"></div>
          </div>
          <PoolImage />
        </div>
      </div>
    </>
  );
};

export default SecondPool;
